<template>
  <div style="padding-top:50px;">
  <!---->
  <div class="bd-example">
    <div id="carouselExampleInterval" class="carousel slide" data-ride="carousel">
      <ol class="carousel-indicators">
        <li data-target="#carouselExampleInterval" data-slide-to="0" class="active"></li>
        <li data-target="#carouselExampleInterval" data-slide-to="1"></li>
        <li data-target="#carouselExampleInterval" data-slide-to="2"></li>
      </ol>
      <div class="carousel-inner">
        <div class="carousel-item active"  data-interval="4000">
          <img src="../assets/weeding-bg-1.jpg" alt="wedding">
          <div class="carousel-caption text-left">
            <h5> </h5>
            <p>, , , , , , , , </p>
            <p><a class="btn btn-lg btn-primary" href="#" role="button"></a></p>
          </div>
        </div>
        <div class="carousel-item">
          <img src="../assets/weeding-bg-2.jpg" alt="wedding">
          <div class="carousel-caption text-right">
            <h5></h5>
            <p>~</p>
            <p><a class="btn btn-lg btn-primary" href="#" role="button"></a></p>
          </div>
        </div>
        <div class="carousel-item">
          <img src="../assets/weeding-bg-3.jpg" alt="wedding">
          <div class="carousel-caption text-left">
            <h5></h5>
            <p>, !~</p>
            <p><a class="btn btn-lg btn-primary" href="#" role="button"></a></p>
          </div>
        </div>
      </div>
      <a class="carousel-control-prev" href="#carouselExampleInterval" role="button" data-slide="prev">
        <span class="carousel-control-prev-icon" aria-hidden="true"></span>
        <span class="sr-only">Previous</span>
      </a>
      <a class="carousel-control-next" href="#carouselExampleInterval" role="button" data-slide="next">
        <span class="carousel-control-next-icon" aria-hidden="true"></span>
        <span class="sr-only">Next</span>
      </a>
    </div>
  </div>
  <div class="container text-center">    
    <h3>WHAT WE DO?</h3><br>
    <div class="row hover01">
      <div class="col-sm-4" @click="linkToprd()">
        <div class="figure">
          <img src="../assets/project-1.jpg" class="img-responsive" style="width:100%" alt="">
        </div>
        <p></p>
      </div>
      <div class="col-sm-4" @click="linkToprd()">
        <div class="figure">
          <img src="../assets/project-2.jpg" class="img-responsive" style="width:100%" alt="">
        </div>
        <p></p>    
      </div>
      <div class="col-sm-4" @click="linkToprd()">
        <div class="figure">
          <img src="../assets/project-3.jpg" class="img-responsive" style="width:100%" alt="">
        </div>  
        <p></p>    
      </div>
    </div>
  </div>    
  </div>
</template>
<script>
export default {
  methods : {
    linkToprd() {
      this.$router.push({
				name: 'products_list',
			}); 
    }
  }
}
</script>
<style scoped>
/* Carousel base class */
.carousel {
  margin-bottom: 4rem;
}
/* Since positioning the image, we need to help out the caption */
.carousel-caption {
  bottom: 3rem;
  z-index: 10;
  color:rgb(126, 112, 107);
}

/* Declare heights because of positioning of img element */
.carousel-item {
  height: 32rem;
}
.carousel-item > img {
  position: absolute;
  top: -200px;
  left: 0;
  min-width: 100%;
}

/* zoom in */
.figure {
	width: 350px;
	height: 187px;
	margin: 0;
	padding: 0;
	background: #fff;
  overflow: hidden;
  cursor: pointer;
}
.hover01 .figure img {
	-webkit-transform: scale(1);
	transform: scale(1);
	-webkit-transition: .3s ease-in-out;
	transition: .3s ease-in-out;
}
.hover01 .figure:hover img {
	-webkit-transform: scale(1.3);
	transform: scale(1.3);
}
</style>


